"use client"

import { useState, useEffect } from "react"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Textarea } from "@/components/ui/textarea"
import { Label } from "@/components/ui/label"
import { toast } from "sonner"

export default function AdminAppearancePage() {
  const [banners, setBanners] = useState<any[]>([])
  const [loading, setLoading] = useState(true)
  const [formData, setFormData] = useState({
    title: "",
    message: "",
    link: "",
    type: "info",
    isActive: true,
  })

  useEffect(() => {
    fetchBanners()
  }, [])

  const fetchBanners = async () => {
    try {
      const res = await fetch("/api/banners?all=true")
      const data = await res.json()
      setBanners(data.banners || [])
    } catch (error) {
      toast.error("Failed to load banners")
    } finally {
      setLoading(false)
    }
  }

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    
    if (formData.title.length < 3) {
      toast.error("Title must be at least 3 characters")
      return
    }
    
    if (formData.message.length < 10) {
      toast.error("Message must be at least 10 characters")
      return
    }
    
    try {
      const res = await fetch("/api/admin/banners", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(formData),
      })
      
      const data = await res.json()
      
      if (res.ok) {
        toast.success("Banner created")
        fetchBanners()
        setFormData({ title: "", message: "", link: "", type: "info", isActive: true })
      } else {
        toast.error(data.error || "Failed to create banner")
      }
    } catch (error) {
      toast.error("Failed to create banner")
    }
  }

  const toggleBanner = async (id: string, isActive: boolean) => {
    try {
      const res = await fetch(`/api/admin/banners/${id}`, {
        method: "PATCH",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ is_active: !isActive }),
      })
      
      const data = await res.json()
      
      if (res.ok) {
        toast.success("Banner updated")
        fetchBanners()
      } else {
        toast.error(data.error || "Failed to update banner")
      }
    } catch (error) {
      toast.error("Failed to update banner")
    }
  }

  const deleteBanner = async (id: string) => {
    if (!confirm("Delete this banner?")) return
    try {
      const res = await fetch(`/api/admin/banners/${id}`, { method: "DELETE" })
      const data = await res.json()
      
      if (res.ok) {
        toast.success("Banner deleted")
        fetchBanners()
      } else {
        toast.error(data.error || "Failed to delete banner")
      }
    } catch (error) {
      toast.error("Failed to delete banner")
    }
  }

  return (
    <div className="space-y-6">
      <h1 className="text-3xl font-bold">Appearance Settings</h1>

      <Card>
        <CardHeader>
          <CardTitle>Create Banner</CardTitle>
        </CardHeader>
        <CardContent>
          <form onSubmit={handleSubmit} className="space-y-4">
            <div>
              <Label>Title</Label>
              <Input
                value={formData.title}
                onChange={(e) => setFormData({ ...formData, title: e.target.value })}
                required
              />
            </div>
            <div>
              <Label>Message</Label>
              <Textarea
                value={formData.message}
                onChange={(e) => setFormData({ ...formData, message: e.target.value })}
                required
              />
            </div>
            <div>
              <Label>Link (optional)</Label>
              <Input
                value={formData.link}
                onChange={(e) => setFormData({ ...formData, link: e.target.value })}
              />
            </div>
            <div>
              <Label>Type</Label>
              <select
                className="w-full p-2 border rounded"
                value={formData.type}
                onChange={(e) => setFormData({ ...formData, type: e.target.value })}
              >
                <option value="info">Info</option>
                <option value="warning">Warning</option>
                <option value="success">Success</option>
                <option value="error">Error</option>
              </select>
            </div>
            <Button type="submit">Create Banner</Button>
          </form>
        </CardContent>
      </Card>

      <Card>
        <CardHeader>
          <CardTitle>Active Banners</CardTitle>
        </CardHeader>
        <CardContent>
          {loading ? (
            <p>Loading...</p>
          ) : banners.length === 0 ? (
            <p>No banners</p>
          ) : (
            <div className="space-y-4">
              {banners.map((banner) => (
                <div key={banner.id} className="border p-4 rounded space-y-2">
                  <div className="flex justify-between items-start">
                    <div>
                      <h3 className="font-bold">{banner.title}</h3>
                      <p className="text-sm text-muted-foreground">{banner.message}</p>
                      {banner.link && (
                        <a href={banner.link} className="text-sm text-blue-500">
                          {banner.link}
                        </a>
                      )}
                    </div>
                    <span
                      className={`px-2 py-1 rounded text-xs ${
                        banner.is_active ? "bg-green-500" : "bg-gray-500"
                      } text-white`}
                    >
                      {banner.is_active ? "Active" : "Inactive"}
                    </span>
                  </div>
                  <div className="flex gap-2">
                    <Button size="sm" onClick={() => toggleBanner(banner.id, banner.is_active)}>
                      {banner.is_active ? "Deactivate" : "Activate"}
                    </Button>
                    <Button size="sm" variant="destructive" onClick={() => deleteBanner(banner.id)}>
                      Delete
                    </Button>
                  </div>
                </div>
              ))}
            </div>
          )}
        </CardContent>
      </Card>
    </div>
  )
}
